.utility-nav {
  display: none;
  float: right;
  font-family: $condensed;
  font-size: $font-size-small;
  margin: $larger-vertical 0;
  padding: 0;
  text-transform: uppercase;

  @include breakpoint($tablet) {
    display: block;
  }

  a {
    color: $text;
    padding: 0 $small-horizontal;

    &:hover {
      color: $hover-text;
    }
  }

  li {
    display: inline-block;
    font-size: 13px;
    line-height: 11px;

    &:last-child a {
      padding-right: 0;
    }

    &::before {
      content: none;
    }

    + li {
      border-left: 1px solid $text;
    }
  }
}
