
// NCSU Branding

$black:                  #000;
$white:                  #fff;
$brand-red:         	   #c00 !default;
$brand-reynolds:         #900 !default;
$brand-orange:           #d26c2c !default;
$brand-yellow:         	 #fde148 !default;
$brand-green:         	 #a4b53a !default;
$brand-blue:         	   #47869c !default;
$brand-indigo:           #4156a1 !default;

// ITRE Defaults

$main:                   #333 !default;
$background:             #fff !default;
$accent:                 #333 !default;
$pull:                   darken($background, 5%) !default;
$darken:                 darken($main, 10%) !default;
$text:                   #000 !default;

// Accessibility Text Adjustments

$white-text:             color-compliance($white, $text);
$hover-text:             color-compliance($background, $main);
$hover-darken:           darken($hover-text, 10%);
$hover-accent:           color-compliance($background, $accent);
$accent-darken:          darken($hover-accent, 10%);
$main-text:              color-compliance($main, $text);
$pull-text:              color-compliance($pull, $text);
$accent-text:            color-compliance($accent, $text);
$text:                   color-compliance($background, $text);
