.total-table {
  .article-head {
    margin: 0;
  }

  h3 {
    font-size: $font-size-h6;
    margin: 0;
  }

  h4 {
    font-size: $font-base;
    margin: 0;
  }

  li {
    padding: 0 10px;

    &::before {
      display: none;
    }
  }
}

.article-table {
  border: 1px solid $black;
  border-top: 0;
  display: flex;
  flex-flow: row wrap;
  margin: 0;
  padding: 20px 0;

  .table-authors {
    display: none;

    @include breakpoint($tablet) {
      display: block;
    }
  }
}

.article-head {
  @extend .article-table;
  align-items: center;
  background-color: $main;
  border: 0;
  color: $main-text;

  h3,
  a {
    color: $main-text;
  }

  .table-head,
  .table-authors,
  .table-date,
  .table-links {
    border-right: 1px solid $main-text;
    text-align: center;
  }

  .table-authors:last-child {
    border: 0;
  }

  .table-authors {
    display: none;

    @include breakpoint($tablet) {
      display: block;
    }
  }

  .table-date {
    border: 0;

    @include breakpoint($tablet) {
      border-right: 1px solid $main-text;
    }
  }
}

.table-head {
  flex: 3;
  padding: 0 15px;
}

.table-authors {
  flex: 2;
  padding: 0 10px;
}

.table-date,
.table-links {
  flex: 1;
  text-align: center;
}
