.nav-links {
  display: flex;
}

.nav-previous {
  a {
    .glyphicon {
      display: inline-block;
      left: 0;
      position: relative;
      top: -1px;
      transition: left .2s ease;
      vertical-align: middle;
    }

    &:hover .glyphicon {
      left: -5px;
    }
  }
}

.nav-next {
  margin-left: auto;

  a {
    .glyphicon {
      display: inline-block;
      left: 0;
      position: relative;
      top: -1px;
      transition: left .2s ease;
      vertical-align: middle;
    }

    &:hover .glyphicon {
      left: 5px;
    }
  }
}

.screen-reader-text {
  display: none;
}
