html,
body, {
  background-color: $background;
  color: $text;
  height: 100%;
}

.row {
  display: flex;
  flex-flow: row wrap;
  margin: 0;
}

.wrap.container,
.container {
  display: flex;
  flex-direction: column;
  max-width: 1500px;
  padding: 0 $base-pad;

  @include breakpoint($tablet) {
    width: ($tablet - 20px);
  }

  @include breakpoint($desktop) {
    width: ($desktop - 20px);
  }

  @include breakpoint($large-desktop) {
    width: ($large-desktop - 30px);
  }

  @include breakpoint($max) {
    width: ($max - 30px);
  }
}

.main {
  background-color: $background;
  flex: 3;
  margin: 0 auto;
  order: 1;
  padding-bottom: 0;

  p {
    margin: 0 0 21px;
  }

  &.full {
    flex: 100%;
    margin-bottom: $largest-horizontal;
   }
}

.flex {
  display: flex;
  flex-flow: row wrap;

  .column {
    flex: 1;
    margin: 0 10px;
  }

  .row {
    flex: 100%;
    margin: 0 10px;
    padding: 0;
  }
}
