.bio,
.category {
  align-items: center;
  display: flex;
  flex-flow: row wrap;
  min-height: 120px;
  padding: 0 0 $base-pad;

  .description {
    background-color: $pull;
    color: $pull-text;
    flex: 100%;
    order: 2;
    font-family: $glypha;
    margin: $larger-horizontal 0;
    padding: $larger-horizontal;

    @include breakpoint($tablet) {
      flex: 1;

      &.with-photo {
        margin-left: 50px;
      }
    }
  }
}
