$base-vertical:                    6px !default;
$base-horizontal:                  12px !default;
$base-pad:                         15px !default;

$large-vertical:                   floor(($base-vertical * 1.5)) !default;    // ~9px
$large-horizontal:                 floor(($base-horizontal * 1.5)) !default;  // ~18px

$larger-vertical:                  floor(($base-vertical * 2)) !default;      // ~12px
$larger-horizontal:                floor(($base-horizontal * 2)) !default;    // ~24px

$largest-vertical:                  floor(($base-vertical * 3)) !default;     // ~18px
$largest-horizontal:                floor(($base-horizontal * 3)) !default;   // ~36px

$small-vertical:                   floor(($base-vertical / 1.5)) !default;    // ~4px
$small-horizontal:                 floor(($base-horizontal / 1.5)) !default;  // ~8px

$smaller-vertical:                 floor(($base-vertical / 2)) !default;      // ~3px
$smaller-horizontal:               floor(($base-horizontal / 2)) !default;    // ~6px

$caret-width:                      4px !default;

$table-cell-padding:               8px !default;
$table-bg:                         transparent !default;

$screen-xs:                        480px !default;  // Phone
$screen-sm:                        768px !default;  // Tablet
$screen-md:                        992px !default;  // Desktop
$screen-lg:                        1200px !default; // Wide Desktop
