.more {
  .glyphicon {
    display: inline-block;
    left: 5px;
    position: relative;
    top: -1px;
    transition: left .2s ease;
    vertical-align: middle;
  }

  &:hover .glyphicon {
    left: 10px;
  }
}

.glyphicon {
  transition: left .2s ease;
  vertical-align: middle;
}

.post_head:hover + .entry-summary p .glyphicon {
  color: $main;
  left: 5px;
}
