.banner {
  border: 0;
}

.site-title {
  border-bottom: 1px solid $pull;
  font-family: $condensed;
  margin: 0;
  padding: 45px 0 15px;

  @include breakpoint($tablet) {
    border: 0;
    padding: 0 0 15px;
  }

  a {
    color: $text;

    &:hover {
      color: $main;
    }
  }
}

.menu-toggle {
  background-color: $background;
  border: 0;
  color: $text;
  display: block;
  float: right;
  font-size: $font-size-h2;
  margin: 5px 20px 0 0;
  padding: 0;

  @include breakpoint($tablet) {
    display: none;
  }
}
