.primary-nav {
  border-bottom: 1px solid $pull;
  display: none;
  font-family: $roman;
  font-size: $font-base;
  margin: 0;
  padding: $base-vertical 0 0;

  @include breakpoint($tablet) {
    display: block;
  }

  a {
    color: $text;
    padding: 0 $base-horizontal;

    &:hover {
      color: $accent;
    }
  }

  li {
    display: inline-block;
    padding: 0 $base-horizontal $base-vertical;

    @include breakpoint($tablet) {
      &:first-child,
      &:first-child a {
        padding-left: 0;
      }
    }

    &::before {
      display: none;
    }

    &:hover ul {
      display: block;
    }
  }
}

.sub-menu {
  background-color: $pull;
  border-top: 4px solid $main;
  color: $pull-text;
  display: none;
  line-height: 1em;
  margin-left: 0;
  margin-top: $base-vertical;
  min-width: 170px;
  padding: 15px 0 0;
  position: absolute;
  width: 250px;
  z-index: 1001;

  li {
    display: block;
    padding: 0 $base-horizontal $base-horizontal;

    a {
      padding: 0;
    }

    &:first-child {
      padding: 0 $base-horizontal $base-horizontal;
    }
  }
}
