.widgets {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 30px;
  overflow: hidden;

  a .glyphicon {
    color: $hover-text;
  }

  a:hover {
    .glyphicon {
      color: $hover-darken;
    }

    .news-image {
      background-color: $black;
    }

    img {
      opacity: .7;
    }
  }
}

.home-left,
.home-right {
  flex: 100%;
  padding: 15px $largest-horizontal 0;

  @include breakpoint($desktop) {
    flex: 1;
    //margin: 20px 0 0;
  }
}

.home-right {
  background-color: $pull;
  color: $pull-text;

  h3,
  h5,
  .glyphicon {
    color: color-compliance($pull, $main);
  }

  a:hover h3,
  a:hover h5,
  a:hover .glyphicon {
    color: darken(color-compliance($pull, $main), 10%);
  }
}

.home-wide {
  flex: 1 100%;
}
