.wp-caption-text {
  color: inherit;
  font-family: $glypha;
  font-size: ms(-1);
  width: 75%;
}

.post-image {
  margin-bottom: 18px;
  max-height: 600px;
  overflow: hidden;

  img {
    height: auto;
    min-width: 100%;
  }
}

.featured-page {
  display: none;
  height: auto;
  margin: 0 auto;
  max-width: (($max - 30px) - ($base-pad * 2));
  width: 100%;

  @include breakpoint($phone) {
    display: block;
  }
}
