$roman: 		  		      'UniversRoman', Arial, sans-serif;
$light: 	              'UniversLight', Arial, sans-serif;
$condensed: 	          'UniversCondensed', Arial, sans-serif;
$glypha:			          'Glypha LT W01 55 Roman', Georgia, serif;
$glyph-icons:			      'ncsu-icons';
$icon-font-path:        '../fonts/';

$ms-base:                 1em;
$ms-ratio:                $major-second;

$font-size-h1:            ms(8);
$font-size-h2:            ms(6);
$font-size-h3:            ms(4);
$font-size-h4:            ms(3);
$font-size-h5:            ms(2);
$font-size-h6:            ms(1);
$font-base:               ms(0);
$font-size-small:         ms(-1);

$line-height-base:        ms(1);

html,
body {
  font: normal $font-base $light;
  text-transform: none;
}

a {
  color: $hover-accent;
  cursor: pointer;
  text-decoration: none;

  .icon {
    background-color: $accent;
    border-color: $accent;
  }

  &:hover,
  &:focus {
    color: $main;
    text-decoration: underline;

    p {
      color: $text;
      display: inline-block;
      text-decoration: none;
    }

    h3,
    h5 {
      color: $accent-darken;
    }

    h1 {
      display: inline-block;
      text-decoration: none;
    }

    span {
      color: $accent;
    }

    .icon span {
      color: $accent;
    }
  }
}

%headers {
  font-weight: 500;
  margin: 10px 0;
}

h1 {
  @extend %headers;
  font-size: $font-size-h1;
  line-height: $font-base;
}

h2 {
  @extend %headers;
  font-size: $font-size-h2;
  line-height: $font-size-h6;
  margin: 20px 0;
}

h3 {
  @extend %headers;
  color: $hover-accent;
  font-size: $font-size-h3;
  line-height: $font-size-h5;
}

h4 {
  @extend %headers;
  font-size: $font-size-h4;
  line-height: $font-size-h4;
}

h5 {
  color: $hover-text;
  font-size: $font-size-h5;
  line-height: $font-size-h5;
  margin: ms(-6) 0;
  text-transform: none;
}

h6 {
  font-size: $font-size-h6;
  font-weight: normal;
  line-height: $font-size-h6 + .2em;
  margin: 0 0 ms(-5);
}

p,
label {
  color: $text;
}

p,
label,
a {
  font-size: $font-base;
  line-height: $font-base + .5em;
}


input,
select {
  color: $text;
  font-size: $font-size-small;
  line-height: $font-size-small + .4em;
}
