.calendar-widget {
  %text {
    margin: 0;
  }

  %small {
    @extend %text;
    font-family: $condensed;
    font-size: $font-size-small;
  }

  h4 {
    border-bottom: 1px solid $pull;
    margin-bottom: 25px;
    padding-bottom: 5px;
  }

  .event-date {
    background-color: $main;
    color: $accent-text;
    font-family: $glypha;

    .date-month {
      font-size: $font-size-h6;
    }

    .date-days {
      font-size: $font-size-h4;
    }
  }

  .calendar-events {
    h4 {
      @extend %text;
      border: 0;
      color: $main;
      font-family: $roman;
      font-size: $font-size-h6;
    }

    h5 {
      @extend %text;
      font-size: $font-base;
    }

    p,
    .event-calendar {
      @extend %small;
    }
  }

  p a,
  .event-calendar a {
    @extend %small;
  }

  a:hover {
    text-decoration: underline;
  }
}
