.content-info {
  background-color: $accent;
  color: $accent-text;
  margin-top: 50px;
  padding: 40px 0;

  .container {
    flex-direction: row;
    flex-flow: wrap row;
  }

  a:hover .glyphicon {
    color: $accent-text;
  }

  section {
    flex: 50%;

    @include breakpoint($tablet) {
      flex: 1;
    }

    a,
    li::before {
      color: $main-text;
      vertical-align: middle;
    }

    li {
      margin: 10px 0;
    }
  }

  .social {
    li {
      margin-bottom: 20px;
    }

    li::before {
      display: none;
    }

    .glyphicon {
      font-size: $font-size-h2;
      margin: -4px 5px 0 0;
    }
  }

  h5 {
    color: $main-text;
  }

  h4 {
    font-family: $condensed;
  }

  p {
    color: $main-text;
  }
}

.ncsu-footer {
  color: $main;

  .container {
    flex-direction: row;
  }

  h4 {
    flex: 1;
    font-family: $condensed;
    font-size: $font-size-h2;
  }

  address {
    flex: 1;
    margin: 25px 0;
  }
}
